<template>
  <el-container class="home_container">
    <el-header>
      <div class="home_title"></div>
      <div class="home_userinfoContainer">
        <div style="float: left">
          {{ nowTime }}
        </div>
        &emsp;
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link home_userinfo">
            {{ currentUserName
            }}<i class="el-icon-arrow-down el-icon--right home_userinfo"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <router-link to="/MenuList">
              <el-dropdown-item
                command="MyArticle"
                style="text-decoration: none"
                ></el-dropdown-item
              >
            </router-link>
            <router-link to="/ms">
              <el-dropdown-item></el-dropdown-item>
            </router-link>
            <el-dropdown-item command="logout" divided
              ></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu
          default-active="0"
          class="el-menu-vertical-demo"
          style="background-color: #c1ffe4"
          router
        >
          <!-- index.js,hiddentrue -->
          <template
            v-for="(item, index) in this.$router.options.routes"
            v-if="!item.hidden"
          >
            <!-- children, -->
            <el-submenu
              :index="index + ''"
              v-if="item.children.length > 1"
              :key="index"
            >
              <template slot="title">
                <i :class="item.iconCls"></i>
                <span>{{ item.name }}</span>
              </template>
              <el-menu-item
                v-for="child in item.children"
                v-if="!child.hidden"
                :index="child.path"
                :key="child.path"
              >
                {{ child.name }}
              </el-menu-item>
            </el-submenu>
            <template v-else>
              <el-menu-item :index="item.children[0].path">
                <i :class="item.children[0].iconCls"></i>
                <span slot="title">{{ item.children[0].name }}</span>
              </el-menu-item>
            </template>
          </template>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
              ></el-breadcrumb-item
            >
            <el-breadcrumb-item
              v-text="this.$router.currentRoute.name"
            ></el-breadcrumb-item>
          </el-breadcrumb>
          <keep-alive>
            <router-view v-if="this.$route.meta.keepAlive"></router-view>
          </keep-alive>
          <router-view v-if="!this.$route.meta.keepAlive"></router-view>
        </el-main>
      </el-container>
    </el-container>   
     
  </el-container>
</template>

 
<script>
import { getRequest } from '../utils/api'
export default {
  methods: {
    timeFormate (timeStamp) {
      let year = new Date(timeStamp).getFullYear()
      let month = new Date(timeStamp).getMonth() + 1 < 10 ? '0' + (new Date(timeStamp).getMonth() + 1) : new Date(timeStamp).getMonth() + 1
      let date = new Date(timeStamp).getDate() < 10 ? '0' + new Date(timeStamp).getDate() : new Date(timeStamp).getDate()
      let hh = new Date(timeStamp).getHours() < 10 ? '0' + new Date(timeStamp).getHours() : new Date(timeStamp).getHours()
      let mm = new Date(timeStamp).getMinutes() < 10 ? '0' + new Date(timeStamp).getMinutes() : new Date(timeStamp).getMinutes()
      let ss = new Date(timeStamp).getSeconds() < 10 ? '0' + new Date(timeStamp).getSeconds() : new Date(timeStamp).getSeconds()
      let week = new Date(timeStamp).getDay()
      let weeks = ['', '', '', '', '', '', '']
      let getWeek = '' + weeks[week]
      this.nowTime = year + '' + month + '' + date + '' + ' ' + hh + ':' + mm + ':' + ss + getWeek
    }, // 
    nowTimes () {
      this.timeFormate(new Date())
      setInterval(this.nowTimes, 1000)
      this.clear()
    },
    clear () {
      clearInterval(this.nowTimes)
      this.nowTimes = null
    },

    beforeDestroy () {
      if (this.timer) {
        clearInterval(this.timer); // Vue
      }
    },
    handleCommand (command) {
      var _this = this;

      if (command == 'logout') {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(function () {
          getRequest("/logout")
          _this.currentUserName = '';
          _this.$router.replace({ path: '/' });
        }, function () {
          //
        })
      }
    }
  },
  mounted: function () {
    console.log(this.$router.options.routes, 111)

    var _this = this;
    this.nowTimes();

    getRequest("/currentUserName").then(function (msg) {
      _this.currentUserName = msg.data;
    }, function (msg) {
      _this.currentUserName = '';
    });
  },
  beforeDestroy () {
    this.clear()
  },
  data () {
    return {
      currentUserName: '',
      nowTime: ''
    }
  }
}
</script>
<style>
a {
  text-decoration: none;
}
li {
  text-align: center;
}
.home_container {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

.el-header {
  background-color: gray;
  color: #333;
  text-align: center;
  display: flex;
  height: 20px;
  align-items: center;
  justify-content: space-between;
}

.el-aside {
  background-color: #eee;
}

.el-main {
  background-color: #ffffff;
  color: #000;
  text-align: center;
}

.home_title {
  color: #fff;
  font-size: 22px;
  display: inline;
}

.home_userinfo {
  color: #fff;
  cursor: pointer;
}

.home_userinfoContainer {
  display: inline;
  margin-right: 20px;
}
</style>



