import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import MenuList from '@/components/MenuList'
import PostArticle from '@/components/PostArticle'
import MyOrder from '@/components/MyOrder'
import MenuDetail from '@/components/MenuDetail'
import Register from '@/components/Register'
import Message from '@/components/Message'
import ReceiveList from '@/components/ReceiveList'
import MenuMangeList from '@/components/MenuMangeList'
import MyCurOrder from '@/components/MyCurOrder'
import OrdersManage from '@/components/OrdersManage'
import MoneyManage from '@/components/MoneyManage'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/ms',
      name: '',
      hidden: true,
      component: Message
    },
    {
      path: '/',
      name: '',
      hidden: true,
      component: Login
    }, {
      path: '/re',
      component: Register,
      name: '',
      hidden: true
    }, {
      path: '/home',
      name: '',
      component: Home,
      hidden: true
    },
    {
      path: '/MenuList',
      component: Home,
      name: '',
      children: [{
        path: '/user',
        iconCls: 'el-icon-food',
        name: '',
        component: MenuList,
        meta: {
          keepAlive: true
        }
      }]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      iconCls: 'el-icon-s-goods',
      children: [{
        path: '/MenuList',
        name: '',
        component: MenuList,
        meta: {
          keepAlive: true
        }
      }, {
        path: '/postArticle',
        name: '',
        component: PostArticle,
        meta: {
          keepAlive: false
        }
      }, {
        path: '/MenuDetail',
        name: '',
        component: MenuDetail,
        hidden: true,
        meta: {
          keepAlive: false
        }
      }, ]
    },

    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MyCurOrder',
        iconCls: 'el-icon-tickets',
        name: '()',
        component: MyCurOrder
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MyOrder',
        iconCls: 'el-icon-tickets',
        name: '',
        component: MyOrder
      }]
    },

    {
      path: '/home',
      component: Home,
      children: [{
        path: '/jiedan',
        iconCls: 'el-icon-document-add',
        name: '',
        component: ReceiveList
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MenuMangeList',
        iconCls: 'el-icon-notebook-2',
        name: '',
        component: MenuMangeList
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/OrdersManage',
        iconCls: 'el-icon-collection',
        name: '',
        component: OrdersManage
      }]
    },
     {
       path: '/Home',
       component: Home,
       children: [{
         path: '/MoneyManage',
         iconCls: 'el-icon-collection',
         name: '',
         component: MoneyManage
       }]
     }

  ]
})


