<template>
  <!--    -->
  <el-dialog title="" :visible.sync="dialogVisible" width="30%">
    <el-row v-loading="loading">
      <el-col :span="24">
        <div style="text-align: left">
          <el-button
            type="text"
            icon="el-icon-back"
            @click="goBack"
            style="padding-bottom: 0px"
            ></el-button
          >
        </div>
      </el-col>
      <img :src="this.$route.query.img" />
    </el-row>
    <br />
    <!--        -->
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="curpassword">
        <el-input
          type="password"
          v-model="ruleForm.curpassword"
          autocomplete="off"
        ></el-input>
      </el-form-item>

      <el-form-item :model="ruleForm" label="" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          ></el-button
        >
        <el-button @click="goBack"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
import { getRequest } from '../utils/api';
import { postRequest } from '../utils/api';
export default {
  data () {
    return {
      ruleForm: {
        username: '',
        password: '',
        curpassword: '',
        phone: '',
        email: ''
      },
      dialogVisible: true,
      rules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            type: 'string',
            message: '',
            trigger: 'blur',
            transform (value) {
              if (!/^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/.test(value)) {
                return true
              } else {
              }
            }
          },
          { type: 'string', message: '30', trigger: 'blur', max: 30 }
        ],
        phone:[
          { required: true, message: '', trigger: 'blur' },
          { min: 11, max: 11, message: '11', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 11, message: '311', trigger: 'blur' }
        ],
        curpassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 11, message: '311', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
        ],

      }
    };
  },
  mounted () {
    this.selectUserMsg();
  },
  methods: {
    selectUserMsg: function () {
      var _this = this;
      getRequest('/currentUserMsg').then(resp => {
        _this.ruleForm = resp.data

      }), resp => {
        if (resp.response.status == 403) {
          _this.$message({
            type: 'error',
            message: resp.response.data
          });
        }
      }
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var _this = this;
          postRequest('/updateMsg', {
            username: this.ruleForm.username,
            password: this.ruleForm.password,
            curpassword: this.ruleForm.curpassword,
            phone: this.ruleForm.phone,
            email: this.ruleForm.email,
          }).then(resp => {
            if (resp.status == 200) {
              if (resp.data.status == 'success') {
                _this.$alert(resp.data.msg, '');
                _this.$router.replace({ path: '/home' });
              } else {
                _this.$alert(resp.data.msg, '');
              }
            } else {
              _this.$alert('', '');
            }
          }

          );
        } else {
          _this.$alert('', '')
          return false;
        }
      });

    },
    goBack () {
      // this.$router.replace({ path: '/home' });
      this.$router.go(-1);
    }
  },

}
</script>
 


